@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&family=Urbanist:wght@100;200;300;400;500;600;700;800&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}
body {
  line-height: 1.5;
  font-family: "Roboto", sans-serif;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
  background: transparent;
}
input,
button,
textarea,
select {
  font: inherit;
  outline: none;
  background: transparent;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
  background: transparent;
  font-size: 14px;
}

video {
  max-width: 1536px;
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.container {
  max-width: 1330px;
  margin: 0 auto;
}

.share-icon,
.menu-icon,
.fullscreen-icon {
  width: 22px;
  height: 22px;
  opacity: 1;
  transition: all 0.4s linear;
  cursor: pointer;
  color: white;
}
.hidden-input {
  border: 0;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  position: absolute;
}

.box {
  width: 100% !important;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.no-scrollbar {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
